.App {
  display: flex;
  justify-content: center;
  column-gap: 12px;
  row-gap: 12px;
  margin: 100px auto;
  width: 1000px;
  max-width: 100vw;
  flex-wrap: wrap;
}
@media (max-width: 1080px) {
  .App {
    width: 650px;
  }
}
@media (max-width: 700px) {
  .App {
    width: 350px;
  }
}